import { Grid } from "@mui/material"
import {Box} from "@mui/material"
import DotDivider from "./DotDivider"
export default function TitleCarouselItem({title, img}){
    return(
        //для изменения размера элементов слайдера надо увеличить Box, который содержит img, а также сам img
        <Box sx={{height:'100%', width:'auto', display:'flex', flexDirection:'column',  color: 'white'}}>
            <Box sx={{flexGrow:'1', maxHeight:'220px', maxWidth:'153px'}}>
             <img width="auto" height="100%" src={img} style={{flexGrow:'1', maxHeight:'220px', maxWidth:'153px'}}></img>
            </Box>
            <Box>
                <span>{title}</span><DotDivider color="#51bac4"/><span style={{opacity:'0.6'}}>Serial</span>
            </Box>
        </Box>
    )
}