import { Routes, Route } from 'react-router-dom';
import AnimePage from './anime';
import AboutPage from './about';
import { Link } from 'react-router-dom';
import { AppBar } from '@mui/material';
import { Container, Box } from '@mui/system';
import {IconButton} from '@mui/material';
import { Menu } from '@mui/icons-material';
import {Toolbar} from '@mui/material';
import {Button} from '@mui/material';
import {Avatar} from '@mui/material';
import { Typography } from '@mui/material';


export default function HeaderNav(){
    const navItems = [{title: 'Главная', path: '/'}, {title: 'О нас', path: '/about'}]

    return(
        <>
         <AppBar className='app-nav-menu' sx={{background: '#1c1c1c'}}>
      <Toolbar disableGutters sx={{minHeight:'58px'}}>
        <IconButton sx={{color: 'white', display: {xs: 'flex', md: 'none'}}}>
           <Menu color='red' fontSize='large'/>
         </IconButton>
         <Box sx={{display: {xs: 'none', md: 'flex'}, alignItems: 'center'}}>
            <img src='https://img.icons8.com/arcade/1x/naruto-sign.png' width="55" height="55"></img>
            <Typography variant='h6' sx={{paddingBottom: '4px'}}>SomeName</Typography>
         </Box>
          <Box sx={{flexGrow: '1',display: {xs: 'none', md: 'flex'},padding: '0px 15px'}}>
              {navItems.map((item)=>(
                <Button component={Link} to={item.path} variant='text' sx={{color: 'whitesmoke', fontSize: '13px', padding: '10px 10px', height: '70%',
                "&:hover": {background: '#393e41'},
               }}>{item.title}</Button>
              ))}
              
          </Box>

          <Box sx={{flexGrow: '1' , gap: '5px',display: {xs: 'flex', md: 'none'}, justifyContent: 'center', alignItems: 'center'}}>
          <img src='https://img.icons8.com/arcade/1x/naruto-sign.png' width="45" height="45"></img>
            <Typography variant='h6' sx={{paddingBottom: '4px'}}>SomeName</Typography>
          </Box>

          <Box>
            <IconButton>
               <Avatar src='https://i.pinimg.com/736x/10/99/50/1099509dcced88a6224a14813aceccf2.jpg' sx={{height: 50, width: 50}} />
            </IconButton>
          </Box>

      </Toolbar>
    </AppBar>
        </>
    )
}